.about-better {
  padding-top: 72px;
  padding-bottom: 72px;

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &__title {
    text-align: center;
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__row {
    max-width: 1056px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &:nth-child(even) {
      flex-direction: row-reverse;
    }

    &:not(:last-child) {
      margin-bottom: 72px;
    }

    @include medium-tablet {
      &:not(:last-child) {
        margin-bottom: 52px;
      }
    }

    @include small-tablet {
      flex-direction: column;

      &:nth-child(even) {
        flex-direction: column;
      }

      &:not(:last-child) {
        margin-bottom: 42px;
      }
    }

    @include mobile {
      &:not(:last-child) {
        margin-bottom: 32px;
      }
    }
  }

  &__img {
    width: 518px;
    height: auto;

    @include medium-tablet {
      width: 400px;
    }

    @include small-tablet {
      width: 300px;
      margin-bottom: 24px;
      background-color: var(--grey-500);
    }

    @include mobile {
      width: 250px;
      margin-bottom: 16px;
      background-color: var(--grey-500);
    }
  }

  &__content {
    max-width: 410px;

    @include medium-tablet {
      max-width: 350px;
    }

    &-title {
      margin-bottom: 24px;

      @include medium-tablet {
        font-size: 26px;
      }
      @include small-tablet {
        text-align: center;
        font-size: 22px;
        margin-bottom: 12px;
      }
      @include mobile {
        font-size: 20px;
      }
    }

    &-descr {
      @include medium-tablet {
        font-size: 20px;
      }
      @include small-tablet {
        text-align: center;
        font-size: 18px;
      }
      @include mobile {
        font-size: 16px;
      }
    }
  }
}
