.btn {
  display: inline-flex;
  border-radius: 8px;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  color: var(--base-grey);
}

.fill {
  background-color: var(--cyan-400);
  transition: background-color 0.3s ease-in;

  &:hover {
    background-color: var(--cyan-200);
  }
}

.secondary {
  background-color: var(--grey-200);
  transition: background-color 0.3s ease-in;

  &:hover {
    background-color: var(--cyan-400);
  }
}

.big {
  padding: 18px 36px;
}

.medium {
  padding: 12px 24px;
}

.small {
  padding: 8px 12px;
}

.link {
  align-items: center;
  border-radius: 0;
  padding: 12px 0;
  transition: color 0.3s ease-in;

  i {
    font-size: 16px;
    margin-left: 8px;
    margin-bottom: 1px;
    transition: color 0.3s ease-in;
  }

  &:hover {
    color: var(--cyan-400);

    i {
      color: var(--cyan-400);
    }
  }
}

.mob-header__btn {
  padding-top: 18px;
  padding-bottom: 18px;
  text-align: center;
  justify-content: center;
  width: 100%;
}

.border {
  border: 2px solid var(--cyan-500);
  transition: color 0.3s ease-in;

  &:hover {
    color: var(--cyan-500);
  }
}
