.interface {
  padding-top: 24px;
  background-color: var(--grey-100);

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__img {
    max-width: 1062px;
    margin-left: auto;
    margin-right: auto;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;

    @include mobile {
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
    }

    @media (max-width: 390px) {
      margin-right: -24px;
      border-top-right-radius: 0;
    }

    img {
      display: block;
      height: 100%;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      box-shadow: 0px -8px 22px -6px rgba(70, 99, 92, 0.12);

      @include mobile {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
      }

      @media (max-width: 390px) {
        border-top-right-radius: 0;
      }
    }
  }

  .image {
    @media (max-width: 390px) {
      display: none;
    }
  }
  .image__mob {
    @media (min-width: 390px) {
      display: none;
    }
  }
}
