:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --font-weight: normal;
  --content-width: 1296px;
  --container-offset: 24px;
  // --containerMobile-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --base-white: #FFFFFF;
  --base-black: #000000;
  --base-grey: #212121;

  --grey-900: #1F2937;
  --grey-800: #374151;
  --grey-600: #4B5563;
  --grey-500: #6D7280;
  --grey-400: #9CA3AF;
  --grey-300: #D2D5DA;
  --grey-200: #E5E7EB;
  --grey-100: #F3F4F6;

  --cyan-500: #00E3AA;
  --cyan-400: #1DE9B6;
  --cyan-300: #46EDC3;
  --cyan-200: #75F1D2;

  --blue-500: #0F6CE3;
  --blue-400: #2E80E9;
  --blue-300: #5497ED;
  --blue-200: #7FB1F1;

  --yellow-500: #FF9E00;
  --yellow-400: #FFAA20;
  --yellow-300: #FFBB4C;
  --yellow-200: #FFCD7C;

  --orange-500: #FF5F00;
  --orange-400: #FF7320;
  --orange-300: #FF8E4C;
  --orange-200: #FFAC7C;

  --red-500: #FC003F;
  --red-400: #FF245B;
  --red-300: #FF507C;
  --red-200: #FF81A1;

  // shadows
  --shadow-1: 0px 6px 14px -6px rgba(70, 99, 92, 0.12), 0px 10px 32px -4px rgba(70, 99, 92, 0.1);
  --shadow-2: 0px 8px 18px -6px rgba(70, 99, 92, 0.12), 0px 12px 42px -4px rgba(70, 99, 92, 0.12);
  --shadow-3: 0px 8px 22px -6px rgba(70, 99, 92, 0.12), 0px 14px 64px -4px rgba(70, 99, 92, 0.12);
  --shadow-4: 0px 8px 28px -6px rgba(24, 39, 75, 0.12), 0px 18px 88px -4px rgba(70, 99, 92, 0.14);
}
