.graph-modal__close {
  right: -38px;
  top: 10px;
  width: 28px;
  height: 28px;
  font-size: 16px;
  line-height: 1;
  text-align: center;
  color: var(--base-white);
  border-radius: 100%;
  background-color: var(--grey-500);

  @include small-tablet {
    right: 20px;
    top: 20px;
  }
}

.graph-modal__container {
  padding: 44px;
  max-width: 622px;

  @include mobile {
    padding: 30px;
  }

  @media (max-width: 360px) {
    padding: 20px;
  }
}

.modal__title {
  margin-bottom: 12px;
}

.modal__descr {
  max-width: 474px;
  margin-bottom: 24px;

  @include mobile {
    font-size: 16px;
    margin-bottom: 12px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  width: 100%;

  &__label {
    display: flex;
    flex-direction: column;
    padding: 16px;
    width: 100%;

    @include mobile {
      padding: 10px 0;
    }

    &-title {
      font-weight: 600;
      font-size: 16px;
      line-height: 20px;
      color: var(--base-grey);
      margin-bottom: 8px;

      @include mobile {
        font-size: 14px;
        line-height: 18px;
      }
    }
  }

  &__input {
    border-radius: 6px;
    width: 100%;
    height: 56px;
    padding: 12px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--base-grey);
    background-color: #F8F8F8;
    border: 1px solid var(--grey-300);

    @include mobile {
      height: 46px;
      font-size: 14px;
    }

    &--textarea {
      max-width: 100%;
      height: 108px;
      min-height: 56px;

      @include mobile {
        height: 70px;
      }
    }

    &::placeholder {
      color: var(--grey-500);
    }

    &:focus {
      outline: none;
      border: 1px solid var(--cyan-400);
    }
  }

  &__btn {
    margin-top: 24px;
    max-width: 164px;

    @include mobile {
      margin-top: 12px;
      max-width: 100%;
      text-align: center;
      justify-content: center;
    }
  }
}

.form-group {
  position: relative;
  display: flex;
  width: 100%;

  &>span,
  .form__input--fluid {
    white-space: nowrap;
    display: block;
    border-left: none;

    &:first-child {
      border-radius: 6px 0 0 6px;
    }

    &:last-child {
      border-radius: 0 6px 6px 0;
    }

    &:not(:first-child) {
      margin-left: -1px;
    }
  }

  &>span {
    position: relative;
    text-align: center;
    padding: 10px 8px;
    width: 56px;
    height: 56px;
    background: #F8F8F8;
    border: 1px solid var(--grey-300);
    border-right: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 36px;
    color: var(--base-black);
    transition: border 0.3s ease, color 0.3s ease;

    &::before {
      position: absolute;
      content: "";
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 1px;
      height: 38px;
      background: rgba(0, 0, 0, 0.12);
      border-radius: 1px;
    }

    @include mobile {
      width: 46px;
      height: 46px;
      font-size: 14px;
      line-height: 26px;
      &::before {
        height: 28px;
      }
    }
  }

  &:focus-within {
    &>span {
      color: var(--base-grey);
      border-color: var(--cyan-400);
    }
  }
}

.choices__list--single {
  @include mobile {
    padding: 0;
  }
}

.choices__inner {
  width: 100%;
  height: 56px;
  padding: 12px;
  border-radius: 6px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--base-grey);
  border: 1px solid var(--grey-300) !important;
  background-color: #F8F8F8 !important;
  min-height: auto;

  @include mobile {
    height: 46px;
    font-size: 14px;
    line-height: 20px;
  }
}

.is-open .choices__inner {
  width: 100%;
  height: 56px;
  padding: 12px;
  border-radius: 6px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--base-grey);
  border: 1px solid var(--grey-300) !important;
  background-color: #F8F8F8 !important;
  min-height: auto;

  @include mobile {
    padding: 12px;
    height: 46px;
    font-size: 14px;
    line-height: 20px;
  }
}

.choices__list--dropdown {
  width: 100%;
  border-radius: 6px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--base-grey);
  border: 1px solid var(--grey-300) !important;
  background-color: #F8F8F8 !important;

  @include mobile {
    font-size: 14px;
    line-height: 20px;
  }
}

.is-open .choices__list--dropdown {
  width: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--base-grey);
  border: 1px solid var(--grey-300) !important;
  background-color: #F8F8F8 !important;
  border-radius: 0;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;

  @include mobile {
    font-size: 14px;
    line-height: 20px;
  }
}

.choices__list--dropdown .choices__item--selectable.is-highlighted:after {
  display: none;
}

input[type="file"] {
  display: none;
}

.custom-file-upload {
  display: inline-block;
  padding: 16px;
  border: 1px dashed var(--grey-400);
  border-radius: 12px;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--grey-500);
  cursor: pointer;

  @include mobile {
    padding: 10px 12px;
    font-size: 14px;
    line-height: 20px;
  }

  span {
    color: var(--blue-500);
    text-decoration: none;
    transition: color 0.3s ease;

    &:hover {
      color: var(--cyan-400);
    }
  }
}

#file_name {
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: var(--base-grey);

  @include mobile {
    font-size: 14px;
    line-height: 20px;
  }
}
