.footer {
  &__cta {
    width: 100%;
    background: linear-gradient(to bottom, var(--base-white) 50%, var(--base-black) 50%);

    &-wrapper {
      position: relative;
      margin: 0 auto;
      padding: 72px;
      max-width: 1076px;
      border-radius: 24px;
      background-color: var(--base-grey);
      box-shadow: 0px 24px 32px -8px rgba(0, 0, 0, 0.2);
      z-index: 0;

      @include tablet {
        padding: 50px 40px 50px 40px;
      }

      @media (max-width: 820px) {
        padding: 40px 35px 40px 35px;
      }

      @include small-tablet {
        padding: 24px;
        border-radius: 12px;
      }

      &::before {
        position: absolute;
        content: "";
        top: 0px;
        right: 0px;
        width: 480px;
        height: 100%;
        border-top-right-radius: 24px;
        border-bottom-right-radius: 24px;
        background-image: url('../img/footer/footer__cta-img.png');
        background-repeat: no-repeat;
        z-index: -1;

        @media (max-width: 820px) {
          display: none;
        }
      }
    }

    &-content {
      max-width: 488px;
      z-index: 100;

      @include tablet {
        max-width: 400px;
      }

      @include medium-tablet {
        max-width: 330px;
      }

      @media (max-width: 820px) {
        max-width: 100%;
      }
    }

    &-title {
      color: var(--base-white);
      margin-bottom: 12px;

      @include medium-tablet {
        font-size: 26px;
      }

      @include mobile {
        display: none;
      }
    }

    &-descr {
      color: var(--grey-300);
      margin-bottom: 33px;

      @include medium-tablet {
        font-size: 16px;
      }

      @media (max-width: 820px) {
        margin-bottom: 24px;
      }

      @include mobile {
        display: none;
      }
    }

    &-title--mob {
      @media (min-width: 577px) {
        display: none;
      }
      max-width: 190px;
      font-size: 24px;
      color: var(--base-white);
      margin-bottom: 12px;
    }

    &-descr--mob {
      @media (min-width: 577px) {
        display: none;
      }
      max-width: 290px;
      font-size: 16px;
      color: var(--grey-300);
      margin-bottom: 24px;
    }

    &-btn {
      @include small-tablet {
        width: 100%;
        text-align: center;
        justify-content: center;
      }
    }
  }

  &__wrapper {
    padding-top: 48px;
    padding-bottom: 32px;
    background-color: var(--base-black);

    @include medium-tablet {
      padding-bottom: 48px;
    }
  }

  &__nav {
    max-width: 1076px;
    min-height: 294px;
    margin: 0 auto;
    margin-bottom: 48px;
    display: flex;

    @include mobile {
      flex-direction: column;
      margin-bottom: 72px;
    }
  }

  &__nav-column:not(:last-child) {
    margin-right: 92px;

    @include small-tablet {
      margin-right: 60px;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 32px;
    }
  }

  &__nav-title {
    font-weight: 500;
    font-size: 24px;
    line-height: 117%;
    color: var(--base-white);
    margin-bottom: 12px;
  }

  &__nav-item {
    margin-bottom: 12px;
  }

  &__nav-link {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-500);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--cyan-400);
    }
  }

  &__nav-item--fluid {
    margin-bottom: 24px;
  }

  &__nav-link--fluid {
    font-weight: 500;
    font-size: 24px;
    line-height: 117%;
    color: var(--base-white);
  }

  .logo {
    margin-right: 26px;

    @include medium-tablet {
      margin-right: 0;
      margin-bottom: 24px;
    }
  }

  &__info {
    max-width: 1076px;
    margin: 0 auto;
    display: flex;
    align-items: center;

    @include medium-tablet {
      flex-direction: column;
      align-items: start;
    }
  }

  &__address, &__phone {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-500);
  }

  &__address {
    max-width: 522px;
    margin-right: auto;

    @include medium-tablet {
      max-width: 100%;
      margin-bottom: 24px;
    }
  }

  &__phone {
    max-width: 194px;

    @include medium-tablet {
      max-width: 100%;
    }

    &:not(:last-child) {
      margin-right: auto;

      @include medium-tablet {
        margin-bottom: 24px;
      }
    }
  }

  &__phone-link {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-500);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--cyan-400);
    }
  }
}
