.about-main {
  position: relative;
  padding-top: 48px;
  background-color: var(--base-black);
  overflow: hidden;
  z-index: 0;

  @include small-tablet {
    padding-top: 32px;
  }

  .experts {
    background: none;

    @include small-tablet {
      padding-top: 32px;
    }
  }

  &::before {
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
    width: 573px;
    height: 349px;
    background-image: url('../img/page-about/logo-decor.svg');
    background-repeat: no-repeat;

    @include mobile {
      display: none;
    }
  }

  &::after {
    position: absolute;
    content: "";
    left: 50%;
    transform: translateX(-50%);
    bottom: -1330px;
    width: 1330px;
    height: 1330px;
    background: radial-gradient(50% 50% at 50% 50%, #1DE9B6 0%, #1DE9E1 100%);
    filter: blur(512px);
    z-index: -1;
  }
}

.about-banner {
  display: flex;
  justify-content: space-between;
  border: 2px solid var(--base-grey);
  border-radius: 24px;
}

.about-banner__box {
  padding: 72px 10px 72px 72px;

  @media (max-width: 1120px) {
    padding: 30px 5px 30px 30px;
  }

  @include mobile {
    padding: 24px;
  }
}

.about-banner__content {
  max-width: 490px;

  @media (max-width: 1120px) {
    max-width: 380px;
  }

  @include mobile {
    max-width: 100%;
  }

  .title1 {
    color: var(--base-white);
    margin-bottom: 12px;

    @media (max-width: 1120px) {
      font-size: 40px;
    }

    @include mobile {
      font-size: 32px;
    }
  }

  .descr2 {
    color: var(--base-white);
    @media (max-width: 1120px) {
      font-size: 20px;
    }
    @include mobile {
      font-size: 18px;
    }
  }
}

.about-banner__img {
  width: 480px;
  height: auto;

  @include medium-tablet {
    width: 400px;
  }

  @include mobile {
    display: none;
  }
}
