.industries {
  padding-top: 72px;
  padding-bottom: 72px;

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;

    @include medium-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__card {
    padding: 24px;
    background-color: var(--base-white);
    border: 2px solid var(--grey-200);
    border-radius: 24px;
    transition: background-color 0.3s ease-in, border 0.3s ease-in;

    @include mobile {
      padding: 20px 24px;
    }

    &:hover {
      background-color: var(--grey-100);
      border: 2px solid var(--grey-100);
    }
  }

  &__card-link {
    @include mobile {
      display: flex;
      align-items: center;
    }
  }

  &__card-img {
    min-width: 48px;
    min-height: 48px;
    margin-bottom: 24px;

    @include mobile {
      margin-bottom: 0;
      margin-right: 24px;
    }
  }

  &__card-title {
    margin-bottom: 12px;

    @include mobile {
      margin-bottom: 0;
    }
  }

  .descr4 {
    @include mobile {
      display: none;
    }
  }
}
