.about-mission {
  position: relative;
  padding-top: 72px;
  padding-bottom: 72px;
  background-color: var(--base-black);
  overflow: hidden;
  z-index: 0;

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &::after {
    position: absolute;
    content: "";
    left: 50%;
    transform: translateX(-50%);
    bottom: -1700px;
    width: 1330px;
    height: 1330px;
    background: radial-gradient(50% 50% at 50% 50%, #1DE9B6 0%, #1DE9E1 100%);
    filter: blur(512px);
    z-index: -1;
  }

  &__title {
    color: var(--base-white);
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;

    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__card {
    border: 2px solid #212121;
    border-radius: 24px;
  }

  &__img {
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    width: 100%;

    img {
      border-top-left-radius: 24px;
      border-top-right-radius: 24px;
    }
  }

  &__content {
    padding: 24px 24px 42px 24px;

    @include medium-tablet {
      padding: 24px 24px 36px 24px;
    }

    @include small-tablet {
      padding: 24px;
    }
  }

  &__card-title {
    color: var(--base-white);
    margin-bottom: 12px;

    @include medium-tablet {
      font-size: 28px;
    }

    @include small-tablet {
      font-size: 24px;
    }
  }

  &__list {
    padding-left: 20px;
    margin: 0;
  }

  &__item {
    font-weight: 400;
    font-size: 24px;
    line-height: 150%;
    color: var(--grey-300);

    &::marker {
      color: var(--cyan-400);
    }

    &:not(:last-child) {
      margin-bottom: 6px;
    }

    @include medium-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size: 16px;
    }
  }
}
