.experts {
  padding-top: 72px;
  padding-bottom: 72px;
  background: var(--base-black);
  background: radial-gradient(63.11% 267.11% at 50% 267.11%, #00E3AA 0%, #1DE9E1 28.12%, #000000 100%);

  @include medium-tablet {
    background: radial-gradient(594.88% 99.96% at 600.43% 50.07%, #00E3AA 0%, #1DE9E1 28.12%, #000000 100%);
  }

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
    background: radial-gradient(594.88% 99.96% at 600.43% 50.07%, #00E3AA 0%, #1DE9E1 28.12%, #000000 100%);
  }

  @include mobile {
    position: relative;
    padding-top: 48px;
    padding-bottom: 48px;

    &::before {
      position: absolute;
      content: "";
      bottom: 6px;
      left: 50%;
      transform: translateX(-50%);
      width: 390px;
      height: 240px;
      background-image: url('../img/experts/logo-decor.svg');
      background-repeat: no-repeat;
      background-size: 100% auto;
    }
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    @include medium-tablet {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 32px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    &-title {
      font-weight: 600;
      font-size: 48px;
      line-height: 121.9%;
      color: var(--base-white);

      @include medium-tablet {
        text-align: center;
      }
    }
    hr {
      width: 96px;
      margin: 12px 0;
      padding: 0;
      height: 0;
      border: none;
      border-top: 2px solid var(--cyan-400);

      @include medium-tablet {
        margin: 12px auto 12px auto;
      }

      @include small-tablet {
        margin: 6px auto 6px auto;
      }
    }
    &-descr {
      color: var(--grey-300);

      @include medium-tablet {
        text-align: center;
      }
    }
  }
}
