.mob-header {
  display: none;

  @include small-tablet {
    display: block;
    position: fixed;
    right: 0;
    top: 56px;
    z-index: 500;
    overflow-y: auto;
    padding: 24px 24px 60px 24px;
    width: 334px;
    height: 100vh;
    background-color: var(--base-grey);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
  }

  @media (max-width: 350px) {
    width: 320px;
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__nav-list--list {
    border-left: 1px solid var(--cyan-400);
    margin-bottom: 12px;
  }

  &__nav-item--item {
    padding: 12px 24px;
  }

  &__nav-link--link {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--base-white);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--cyan-400);
    }
  }

  .viewall-btn {
    padding: 12px 24px;
    a {
      color: var(--base-white);

      i {
        color: var(--base-white);
      }
    }
  }

  &__nav-item {
    padding: 14px 16px;
  }

  &__nav-link {
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: var(--base-white);
  }

  .mob-header__btns {
    text-align: center;
    width: 100%;
    justify-content: center;
    margin-bottom: 60px;
  }

  // 

  .accordion__list {
    width: 100%;
  }

  .accordion__control {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 14px 0 14px 16px;
    width: 100%;
    background-color: transparent;
    cursor: pointer;

    i {
      font-size: 24px;
      margin-left: 10px;
      color: var(--base-white);
      transition: transform 0.3s ease-in-out, color 0.3s ease-in;
    }
  }

  .accordion__control::-webkit-details-marker {
    display: none;
  }

  .accordion__content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    padding-left: 20px;
    will-change: max-height;
    transition: all 0.3s ease-out;
    box-sizing: content-box;
  }

  .accordion__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: var(--base-white);
  }

  .open .accordion__control i {
    transform: rotate(180deg);
  }

  .open {
    .accordion__content {
      opacity: 1;
      padding-left: 16px;
      margin-bottom: 24px;
    }
  }
}

.burger {
  @include burger;
  z-index: 501;

  @media (min-width: 769px) {
    display: none;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
