.cases {
  padding-top: 72px;
  padding-bottom: 48px;
  background-color: var(--grey-100);

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &__title {
    margin-bottom: 48px;

    @include small-tablet {
      text-align: center;
      margin-bottom: 32px;
    }
  }

  &__btn {
    text-align: center;
  }

  &__slider {
    padding-left: 24px !important;
    margin-bottom: 32px;
  }

  .swiper-slide {
    width: 412px;
    height: 412px;

    @include small-tablet {
      width: 318px;
      height: auto;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: var(--shadow-1);
    background-color: var(--base-white);
    height: 100%;

    @include small-tablet {
      height: auto;
    }
  }

  &__card-img {
    width: 100%;
    height: 128px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;

    img {
      width: 100%;
      height: 100%;
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
    }
  }

  &__card-box {
    padding: 24px;
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__card-content {
    margin-bottom: 24px;
  }

  &__card-title {
    margin-bottom: 12px;
  }

  &__card-descr {
    display: block;
    display: -webkit-box;
    max-width: 100%;
    -webkit-line-clamp: 3; /*   */
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__card-btn {
    margin-top: auto;

    @include small-tablet {
      width: 100%;
      text-align: center;
      background-color: var(--grey-200);
      border-radius: 8px;

      a {
        width: 100%;
        justify-content: center;
      }
    }
  }

  .link {
    align-items: center;
    border-radius: 0;
    padding: 12px 0;
    transition: color 0.3s ease-in;

    i {
      position: relative;
      font-size: 16px;
      margin-left: 8px;
      margin-bottom: 1px;
      z-index: 5;
      transition: color 0.3s ease-in;

      @include small-tablet {
        display: none;
      }

      &:before {
        z-index: 100;
      }
    }

    &:hover {
      color: var(--cyan-400);

      i {
        color: var(--cyan-400);
        &:before {
          z-index: 5;
        }

        &::after {
          position: absolute;
          content: "";
          width: 24px;
          height: 24px;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          border-radius: 100%;
          background-color: var(--grey-100);
          z-index: -1;
        }
      }
    }
  }


  &__btn1, &__btn2 {
    text-align: center;
  }

  &__btn1 {
    @include small-tablet {
      display: none;
    }
  }

  &__btn2 {
    @media (min-width: 769px) {
      display: none;
    }
    @include small-tablet {
      width: 100%;
      text-align: center;

      a {
        width: 100%;
        justify-content: center;
      }
    }
  }
}
