.blog {
  padding-top: 72px;
  padding-bottom: 72px;

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &__title {
    text-align: center;
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  .posts {
    max-width: 1076px;
    margin: 0 auto;
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &-post {
    padding: 24px;
    border-bottom: solid 1px var(--grey-400);

    @include mobile {
      margin-bottom: 24px;
    }
  }

  &-post__title {
    max-width: 608px;
    margin-bottom: 12px;
  }

  &-post__link {
    color: var(--base-black);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--cyan-400);
    }
  }

  &-post__date {
    color: var(--grey-500);
  }

  &-post__tags {
    display: flex;
    flex-wrap: wrap;
    margin-top: 24px;
  }

  &-post__tag {
    display: inline-flex;
    padding: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 121%;
    min-height: 36px;
    color: var(--base-black);
    border-radius: 12px;
    background-color: var(--grey-200);
    transition: color 0.3s ease-in, background-color 0.3s ease-in;

    &:hover {
      color: var(--base-white);
      background-color: var(--cyan-400);
    }
  }

  &-post__tag {
    margin-bottom: 8px;
  }

  &-post__tag:not(:last-child) {
    margin-right: 8px;
  }

  &__btn {
    text-align: center;

    @include small-tablet {
      width: 100%;

      a {
        width: 100%;
        justify-content: center;
      }
    }
  }
}
