.title1 {
  font-weight: 600;
  font-size: 48px;
  line-height: 121.9%;
  color: var(--base-grey);

  @include small-tablet {
    font-size: 40px;
  }

  @include mobile {
    font-size: 32px;
  }
}

.title2 {
  font-weight: 600;
  font-size: 32px;
  line-height: 121.9%;
  color: var(--base-grey);

  @media (max-width: 420px) {
    font-size: 26px;
  }
}

.title3 {
  font-weight: 600;
  font-size: 24px;
  line-height: 121.9%;
  color: var(--base-grey);

  @include mobile {
    font-size: 20px;
    line-height: 140%;
  }
}

.title4 {
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
  color: var(--base-grey);
}

.descr1 {
  font-weight: 400;
  font-size: 28px;
  line-height: 128.6%;
  color: var(--grey-500);

  @include small-tablet {
    font-size: 22px;
  }

  @include mobile {
    font-size: 18px;
  }
}

.descr2 {
  font-weight: 400;
  font-size: 24px;
  line-height: 150%;
  color: var(--grey-500);
}

.descr3 {
  font-weight: 400;
  font-size: 18px;
  line-height: 155%;
  color: var(--grey-500);
}

.descr4 {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--grey-500);
}
