.hero {
  margin-top: 48px;

  @include small-tablet {
    margin-bottom: 32px;
  }
  
  .swiper {
    overflow: visible !important;
  }
  &__slide-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    // width: 1064px !important;
    height: 480px !important;
    border-radius: 24px;
    background-color: var(--grey-100);
  }
  &__slide-box {
    max-width: 488px;
    display: flex;
    flex-direction: column;
    height: 480px;
  }
  &__slide-content {
    display: flex;
    flex-direction: column;
    padding: 72px 24px 72px 72px;
    height: 480px;
  }
  &__slide-text {
    height: 100%;
    margin-bottom: 15px;
  }
  &__slide-title {
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 48px;
    line-height: 121.9%;
    color: var(--base-grey);
  }
  &__slider-img {
    width: 480px;
    height: 480px;
    border-top-right-radius: 24px;
    border-bottom-right-radius: 24px;

    img {
      border-top-right-radius: 24px;
      border-bottom-right-radius: 24px;
    }
  }

  &__slider {
    height: 564px;
  }
  // .swiper-wrapper {
  //   height: 480px;
  // }
  .swiper-slide {
    width: 1064px !important;
    height: 480px !important;
  }









  .swiper-button-prev::after {
    font-family: 'icons';
    font-size: 24px;
    color: var(--base-black) !important;
    content: "\e97b";
    transition: color 0.3s ease-in;
  }
  .swiper-button-next::after {
    font-family: 'icons';
    font-size: 24px;
    color: var(--base-black) !important;
    content: "\e97c";
    transition: color 0.3s ease-in;
  }
  .swiper-button-prev:hover::after,
  .swiper-button-next:hover::after {
    color: var(--cyan-400) !important;
  }
  .swiper-pagination {
    bottom: 36px !important;
  }
  .swiper-pagination-bullet {
    width: 12px !important;
    height: 12px !important;
    background: var(--grey-300) !important;
    opacity: 1 !important;
  }
  .swiper-pagination-bullet-active {
    background: var(--cyan-400) !important;
  }
}
