.about-vacancy {
  padding-top: 72px;
  padding-bottom: 72px;

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    margin-bottom: 48px;
    max-width: 840px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &-tabs {
    margin-bottom: 32px;
  }

  &__btn {
    text-align: center;

    @include small-tablet {
      width: 100%;

      a {
        width: 100%;
        justify-content: center;
      }
    }
  }

  .tabs__panel {
    display: none;
  }

  .tabs__nav {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 24px;
    display: flex;
    border-bottom: 1px solid var(--grey-200);
    overflow-x: auto;
    white-space: nowrap;
    scrollbar-width: none;
      /*mozilla*/
    &::-webkit-scrollbar {
      display: none;
      /*chrome*/
    }
  }

  .tabs__nav-item:not(:last-child) {
    margin-right: 24px;

    @include mobile {
      margin-right: 16px;
    }
  }

  .tabs__nav-btn {
    padding: 16px 0;
    font-weight: 600;
    font-size: 16px;
    color: var(--grey-400);
    background-color: transparent;

    span {
      margin-left: 8px;
      padding: 2px 8px;
      max-height: 24px;
      font-weight: 500;
      font-size: 13px;
      line-height: 20px;
      color: var(--base-white);
      text-align: center;
      background-color: var(--grey-400);
      border-radius: 20px;
    }
  }

  .tabs__panel--active {
    display: block;
  }

  .tabs__nav-btn--active {
    color: var(--base-grey);
    border-bottom: 2px solid var(--cyan-400);
  }

  // 

  .accordion {
    max-width: 1054px;
    margin: 0 auto;
  }

  .accrodion__box {
    padding: 24px;
    border-bottom: 1px solid var(--grey-400);

    @media (max-width: 380px) {
      padding: 24px 16px;
    }
  }

  .accordion__control {
    display: flex;
    width: 100%;
    background-color: transparent;
    cursor: pointer;
    i {
      font-size: 24px;
      margin-right: 12px;
      color: var(--base-grey);
      transition: transform 0.3s ease-in-out;
    }
  }
  .accordion__control::-webkit-details-marker {
    display: none;
  }
  .accordion__control-heading {
    text-align: left;
  }
  .accordion__control-title {
    margin-bottom: 12px;

    @include mobile {
      margin-bottom: 6px;
    }
  }
  .accordion__control-descr {
    margin-bottom: 12px;
  }

  .accordion__tags {
    display: flex;
    flex-wrap: wrap;
  }
  .accordion__tag {
    display: inline-flex;
    padding: 8px;
    font-size: 18px;
    font-weight: 500;
    line-height: 121%;
    min-height: 38px;
    color: var(--base-black);
    border-radius: 12px;
    background-color: var(--grey-200);
    transition: color 0.3s ease-in, background-color 0.3s ease-in;
    &:hover {
      color: var(--base-white);
      background-color: var(--cyan-400);
    }
    @include mobile {
      font-size: 16px;
      min-height: 36px;
    }
  }
  .accordion__tag {
    margin-bottom: 12px;
    @include mobile {
      margin-bottom: 8px;
    }
  }
  .accordion__tag:not(:last-child) {
    margin-right: 12px;
    @include mobile {
      margin-right: 8px;
    }
  }

  .accordion__content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    transition: all 0.3s ease-out;
    box-sizing: content-box;
  }

  .content__block {
    padding-left: 60px;
    margin-bottom: 24px;
    @include mobile {
      padding-left: 0;
    }
  }
  .accordion__list-title {
    margin-bottom: 12px;
  }
  .accordion__list {
    li {
      position: relative;
      padding-left: 23px;
    }
    li::before {
      position: absolute;
      content:"";
      width: 3px;
      height: 3px;
      top: 12px;
      left: 10px;
      border-radius: 100%;
      background-color: var(--grey-500);
    }
    @media (max-width: 380px) {
      font-size: 16px;
      li {
        padding-left: 15px;
      }
      li::before {
        top: 10px;
        left: 2px;
      }
    }
  }
  .accordion__btn {
    padding-left: 60px;

    @include mobile {
      padding-left: 0;
    }
  }

  .accordion__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: var(--base-grey);
  }

  .open .accordion__control i {
    transform: rotate(180deg);
  }

  .open {
    .accordion__content {
      opacity: 1;
      margin-bottom: 15px;
    }
    .accrodion__box {
      padding: 24px;
      border-bottom: none;

      @include mobile {
        padding: 24px 24px 24px 0;
      }
    }
  }
}
