.about-team {
  padding-top: 72px;
  padding-bottom: 72px;

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    margin-bottom: 48px;
    max-width: 840px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__wrapper {
    padding-left: max(24px, calc((100% - var(--content-width)) / 2));
  }

  .swiper-slide {
    width: 412px;

    @include small-tablet {
      width: 318px;
    }

    @include mobile {
      width: 280px;
    }
  }

  &__img {
    width: 100%;
    margin-bottom: 20px;

    img {
      border-radius: 24px;
      border: 2px solid var(--grey-200);
      background-color: var(--grey-200);
    }

    @include mobile {
      margin-bottom: 16px;

      img {
        border-radius: 12px;
      }
    }
  }

  &__name {
    font-weight: 600;
    font-size: 24px;
    line-height: 121.9%;
    color: var(--base-grey);
    margin-bottom: 12px;

    @include small-tablet {
      font-size: 20px;
      margin-bottom: 6px;
    }
  }

  &__post {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-500);

    @include small-tablet {
      font-size: 16px;
    }
  }
}
