.header {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  background-color: rgba(0, 0, 0, 0.92);

  @include medium-tablet {
    height: 74px;
  }

  @include small-tablet {
    padding-top: 12px;
    padding-bottom: 12px;
    height: auto;
  }

  .logo {
    width: 79px;
    height: 48px;

    @include medium-tablet {
      width: 65px;
      height: 40px;
    }

    @include small-tablet {
      width: 52px;
      height: 32px;
      margin-right: 24px;
      margin-left: auto;
    }
  }

  &__btns {
    display: flex;

    @include small-tablet {
      display: none;
    }
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include small-tablet {
    justify-content: right;
  }
}

.menu {
  display: flex;
  align-items: center;

  @include small-tablet {
    display: none;
  }
}

.search__wrapper {
  width: 80px;
  height: 80px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-right: 24px;

  @include medium-tablet {
    display: none;
  }
}

._icon-SearchLarge, ._icon-Close {
  font-size: 24px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  transition: color 0.3s ease-in;

  &:hover {
    color: var(--cyan-400);
  }
}

// 

.nav {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    padding: 0 8px;
  }

  &__item:not(:last-child) {
    margin-right: 24px;

    @include medium-tablet {
      margin-right: 14px;
    }
  }

  &__link {
    font-weight: 600;
    font-size: 16px;
    color: var(--grey-100);
    line-height: 80px;
    transition: color 0.3s ease-in;

    @include medium-tablet {
      font-size: 15px;
      line-height: 74px;
    }

    &:hover {
      color: var(--cyan-400);
    }
  }

  &__btn {
    border: none;
    font-weight: 600;
    font-size: 16px;
    line-height: 80px;
    color: var(--grey-100);
    display: inline-flex;
    align-items: center;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.3s ease-in;

    i {
      font-size: 16px;
      line-height: 80px;
      margin-left: 8px;
      transition: transform 0.3s ease-in-out, color 0.3s ease-in;
    }

    @include medium-tablet {
      font-size: 15px;
      line-height: 74px;

      i {
        font-size: 15px;
        line-height: 74px;
        margin-left: 6px;
      }
    }
  }

  &__btn:hover {
    color: var(--cyan-400);

    i {
      color: var(--cyan-400);
    }
  }

  &__btn--active {
    color: var(--cyan-400);
    i {
      transform: rotate(180deg);
      color: var(--cyan-400);
    }
  }

  .dropdown {
    position: absolute;
    top: 100%;
    width: 200px;
    background-color: var(--base-grey);
    border-radius: 0 0 8px 8px;
    z-index: 1002;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  .dropdown--active {
    opacity: 1;
    visibility: visible;
  }

  .dropdown__item {
    padding: 12px 15px 15px 15px;

    &:not(:last-child) {
      padding: 12px 15px 5px 15px;
    }
  }

  .dropdown__link {
    display: block;
    width: 100%;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    color: var(--grey-100);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--cyan-400);
    }
  }

  @include small-tablet {
    display: none;
  }
}

// 

header .search-bar-container {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  padding: 15px 15px;
  background: var(--base-grey);
  display: flex;
  align-items: center;
  z-index: 1001;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
}

header .search-bar-container.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

header .search-bar-container #search-bar {
  width: 100%;
  padding: 12px 15px;
  text-transform: none;
  color: var(--base-black);
  font-size: 16px;
  border-radius: 6px;
  border: none;
}

header .search-bar-container label {
  color: #fff;
  cursor: pointer;
  font-size: 30px;
  margin-left: 20px;
}
