.enterprise {
  padding-top: 72px;
  padding-bottom: 72px;
  background-color: var(--grey-100);

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  &__title {
    margin-bottom: 48px;

    @include small-tablet {
      text-align: center;
      margin-bottom: 32px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 412px;
    gap: 24px;
    margin-bottom: 32px;

    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
      grid-auto-rows: auto;
      gap: 32px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: var(--shadow-4);
    background-color: var(--base-white);
    height: 100%;

    @include small-tablet {
      &:nth-child(n+4) {
        display: none;
      }
    }

    &-img {
      width: 100%;
      height: 128px;
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;

      img {
        width: 100%;
        height: 100%;
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
      }

      @include small-tablet {
        display: none;
      }
    }

    &-icon {
      @media (min-width: 768px) {
        display: none;
      }

      width: 48px;
      height: 48px;
      margin-top: 24px;
      margin-left: 24px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    &-box {
      padding: 24px;
      display: flex;
      flex-direction: column;
      height: 100%;
    }

    &-content {
      margin-bottom: 24px;
    }

    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 121.9%;
      margin-bottom: 12px;
    }

    &-descr {
      font-size: 18px;
      line-height: 156%;
      color: var(--grey-500);

      @media (max-width: 420px) {
        font-size: 16px;
      }
    }

    &-btn {
      margin-top: auto;

      @include small-tablet {
        width: 100%;
        text-align: center;

        a {
          width: 100%;
          justify-content: center;
        }
      }
    }
  }

  &__btn1, &__btn2 {
    text-align: center;
  }

  &__btn1 {
    @include small-tablet {
      display: none;
    }
  }

  &__btn2 {
    @media (min-width: 769px) {
      display: none;
    }
    @include small-tablet {
      width: 100%;
      text-align: center;

      a {
        width: 100%;
        justify-content: center;
      }
    }
  }
}
