.about-whyus {
  padding-top: 72px;
  padding-bottom: 48px;

  @include small-tablet {
    padding-top: 56px;
    padding-bottom: 32px;
  }

  @include mobile {
    padding-top: 48px;
    padding-bottom: 24px;
  }

  &__title {
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;

    @include medium-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__card-img {
    min-width: 48px;
    min-height: 48px;
    margin-bottom: 12px;
  }

  &__card-title {
    color: var(--grey-800);
    margin-bottom: 12px;
  }
}
